input , button{
    display: block;
    margin: 10px;
}
.error{
    color: red;
}
.succes{
    color: green;
}
.login{
    border: 1px solid black;
    width: fit-content;
    align-self: center;
    margin-left: 6rem;
    padding: .5rem;
}
#login-button{
    margin-left: 3rem;
    background-color: rgb(124, 124, 220);
    color: white;
    width: 6rem;
    border: none;
}
a{
    text-decoration: none;
}
img{
    margin-left:3rem ;
    width: 20%;
}
.display{
    padding: 10px;
    border: 1px solid rebeccapurple;
    display: flex;
    flex-direction: column;
    margin-left:3rem ;
    width: fit-content;
}